@import "../../bootstrap5/functions";
@import "../../bootstrap5/variables"; 
@import "../../bootstrap5/mixins";

@import "variables";


.cathay-carosel{
  &.#{$prefix}-layout-video-full{
    .slick-slider{
      // width: 960px;
      // margin-left: auto;
      // margin-right: auto;
      @media (min-width: map-get($grid-breakpoints, 'lg')){
        width: 960px;
        margin-left: auto;
        margin-right: auto;
      }
    }


    .slick-list{
      padding:0;
    }
    .slick-track{
      padding-top: 25px;
      padding-bottom: 15px;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        // margin:25px 0 50px;
      }

    }
    .slick-slide{
      border:1px solid #ddd;
      margin:0 10px;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;

      box-shadow: 0px 0px 15px -2px rgba(0,0,0,0.14);
   
      display: flex;
      flex-wrap: wrap;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        
        overflow: hidden;
        padding: 0px;
        >.cs-link{
          display: flex;
          flex-wrap: wrap;
        }
      }

      &:hover,&:active{
        >.cs-link{
          // opacity:1;
        }
        .btn{
          color: #fff;
          background-color: #d5780a;
          border-color: #d5780a;
        }
      }
    }
    .#{$prefix}link{
      display: block;
    }
    .#{$prefix}title{
      display:none;
    }
    .#{$prefix}subtitle{
      display: none;
    }
    .#{$prefix}content{
      display:none;
    }
    .slick-dots{
      bottom:-15px;
    }


    .#{$prefix}txt-wrap{
      display:none;
    }

    .#{$prefix}img-wrap{
      order:20;
      height: 214px;
      min-height: 214px;
      width: 100%;
      background-size:cover;
      overflow: hidden;
      background-repeat: no-repeat;
      background-position: center;
      vertical-align: middle;
      position: relative;
      img{
        visibility: hidden;
      }

      // &:before {
      //   content:'';
      //   position: absolute;
      //   top:0;
      //   bottom:0;
      //   left:0;
      //   right:0;
      //   margin: auto;
      //   display: block;
      //   width: 100px;
      //   height: 100px;
      //   border: 2px solid #fff;
      //   border-radius: 50px;
      // }
      // &:after {
      //   content: "";
      //   display: block;
      //   position: absolute;
      //   top:0;
      //   bottom:0;
      //   left:5px;
      //   right:0;
      //   margin:auto;
      //   width: 10px;
      //   height: 10px;
      //   border-top: 15px solid transparent;
      //   border-bottom: 15px solid transparent;
      //   border-left: 25px solid;
      //   color:#00a94f;
      // }
      
      @media (min-width: map-get($grid-breakpoints, 'md')){
        flex-basis: 100%;
        height: 438px;
      }


      
      @media (min-width: map-get($grid-breakpoints, 'lg')){
        flex-basis: 100%;
        height: 547px;
      }
    }

    .#{$prefix}more{
      display:none;
    }

    .slick-prev,.slick-next {
      top: calc(50% + 35px);
      @media (min-width: map-get($grid-breakpoints, 'md')){
        top: calc(50% - 20px);
      }
    }
    
    .slick-prev {
      @media (min-width: map-get($grid-breakpoints, 'md')){
        left: 10px;
      }
      @media (min-width: map-get($grid-breakpoints, 'lg')){
        left: -15px;
      }
      @media (min-width: map-get($grid-breakpoints, 'xl')){
        left: -8%;
      }
    }
    .slick-next {
      @media (min-width: map-get($grid-breakpoints, 'md')){
        right: 10px;
      }
      @media (min-width: map-get($grid-breakpoints, 'lg')){
        right: -15px;
      }
      @media (min-width: map-get($grid-breakpoints, 'xl')){
        right: -8%;
      }
    }
  }
}